<template>
<div class="fx-finish">
    <header-text :headerText="headerTitle" @header-event="onHeaderBtn" class="fx-header"></header-text>
    <div class="fx-content" >
        是否继续告知？
    </div>
    <div class="btns">
        <div class="item" @click="goContinue()">继续告知</div>
        <div class="item" @click="goFinish()">告知完成</div>
    </div>
</div>
</template>
    
<script>
import Vue from "vue";
import headerText from "@/components/header";
export default {
    name:'issue',
    data(){
        return{
            headerTitle:'智能核保',
            chosedIllIds:[]
        }
    },
    created() {

    },
    methods: {
        //头部点击事件
        onHeaderBtn(text){
            this.$router.go(-1);
        },
        //继续告知
        goContinue(){
            
        },
        //告知完成
        goFinish(){
            
        }
    },
    computed:{},
    components:{
        "header-text":headerText
    },
}
</script>

<style lang="scss">
.fx-finish{
    background:#fff;
    height:100%;
    width:100%;
    .fx-content{
        margin-top: r(156);
        text-align: center;
        height:r(40);
        line-height: r(40);
        font-size: r(34);
        color: #333;
    }
    .btns{
        width:r(690);
        height: r(236);
        margin: 0 auto;
        margin-top: r(68);
        .item{
            width:r(690);
            height:r(88);
            line-height:r(88);
            border-radius: r(10);
            background: #0084ff;
            color: #fff;
            text-align: center;
            margin-bottom: r(30);
            font-size: r(34);
        }
    }
}
</style>


